import React, { FC, Key, useCallback, useEffect, useMemo, useState } from 'react';

import { Button, Checkbox, Modal } from 'antd';
import { CheckboxChangeEvent } from 'antd/es/checkbox';

import { IIndicatorTreeData, ISelectedIndicator } from '../../types';
import { useClassnames } from '../../utils';
import { getAllTreeNodeKeys, getTotalIndicator, getWordShowLength } from '../../utils/handle';

interface IRowTitle {
  first?: string[];
  second?: string[];
  third?: string[];
}

export interface ISetColumnsModal {
  /**
   * modal
   */
  visible: boolean;
  /**
   * 
   */
  isSelectedNumHidden?: boolean;
  /**
   * 
   */
  showSelectAll?: boolean;
  /**
   * 
   */
  defaultTreeData: IIndicatorTreeData[];
  /**
   * list
   */
  defaultSelectedList: ISelectedIndicator[];
  /**
   * 
   */
  onVisibleChange: (show: boolean) => void;
  /**
   * treeData
   */
  updateViewDetail?: (selectedIndicator: ISelectedIndicator[], curIndicatorTreeData: IIndicatorTreeData[]) => void;
}

export const SetColumnsModal: FC<ISetColumnsModal> = props => {
  /**  */
  const [checkedKeys, setCheckedKeys] = useState<Key[]>([]);
  /** ,default */
  const [nativeCheckedKeys, setNativeCheckedKeys] = useState<Key[]>([]);
  /**  default*/
  const [nativeSelectedIndicator, setNativeSelectedIndicator] = useState<ISelectedIndicator[]>([]);
  /**  */
  const [curIndicatorTreeData, setCurIndicatorTreeData] = useState<IIndicatorTreeData[]>([]);
  /**  */
  const [selectedIndicator, setSelectedIndicator] = useState<ISelectedIndicator[]>([]);
  /**  */
  const [nativeAllSelectedIndicators, setNativeAllSelectedIndicators] = useState<ISelectedIndicator[]>([]);
  /**  */
  const [total, setTotal] = useState(0);

  const cn = useClassnames();

  const {
    visible,
    showSelectAll,
    onVisibleChange,
    defaultTreeData,
    defaultSelectedList,
    isSelectedNumHidden,
    updateViewDetail,
  } = props;

  /**  */
  const isDisabledSelectAll = useMemo(() => {
    return nativeAllSelectedIndicators?.every(item => item?.lock);
  }, [nativeAllSelectedIndicators]);

  /**  */
  const isCheckedSelectAll = useMemo(() => {
    return Boolean(nativeAllSelectedIndicators?.length && nativeAllSelectedIndicators?.length === checkedKeys?.length);
  }, [nativeAllSelectedIndicators?.length, checkedKeys?.length]);

  /**  */
  const isIndeterminateSelectAll = useMemo(() => {
    // console.log(
    //   'interrr',
    //   Boolean(
    //     !isDisabledSelectAll &&
    //       nativeAllSelectedIndicators?.length &&
    //       checkedKeys?.length < nativeAllSelectedIndicators?.length
    //   ),
    //   isDisabledSelectAll,
    //   nativeAllSelectedIndicators,
    //   checkedKeys
    // );
    return Boolean(
      !isDisabledSelectAll &&
        nativeAllSelectedIndicators?.length &&
        checkedKeys?.length &&
        checkedKeys?.length < nativeAllSelectedIndicators?.length
    );
  }, [isDisabledSelectAll, nativeAllSelectedIndicators?.length, checkedKeys?.length]);

  /** title */
  const curBlockTitleWidth = useMemo(() => {
    return (
      defaultTreeData?.length &&
      Math.max(
        ...(defaultTreeData?.map(item => {
          return getWordShowLength(item?.title || '', 14) || 0;
        }) || []),
        // 
        showSelectAll ? 56 : 0
      )
    );
  }, [defaultTreeData, showSelectAll]);
  /**  */
  const childBlockWidth = useMemo(() => {
    /** (+padding+title+titlemargin+checkboxmargin) */
    const showMaxWidth = 727 - 17 - 48 - (curBlockTitleWidth || 0) - (curBlockTitleWidth ? 16 : 0);
    /** width(+padding) */
    const extraWidth = 16 + 16;
    /** title */
    const titlesDict =
      defaultTreeData?.reduce((tmp, item) => {
        if (item?.children?.length) {
          item?.children?.forEach((t, index) => {
            if (!(index % 3)) {
              tmp?.first ? tmp.first.push(t?.title || '') : (tmp.first = [t?.title || '']);
            }
            if (index % 3 === 1) {
              tmp?.second ? tmp.second.push(t?.title || '') : (tmp.second = [t?.title || '']);
            }
            if (index % 3 === 2) {
              tmp?.third ? tmp.third.push(t?.title || '') : (tmp.third = [t?.title || '']);
            }
          });
        }
        return tmp;
      }, {} as IRowTitle) || ({} as IRowTitle);

    /**  */
    function getFinalTitleMaxWidth(attr: keyof IRowTitle) {
      const textMaxWidth = Math.max(
        ...(titlesDict?.[attr]?.map(item => {
          return getWordShowLength(item || '', 14) || 0;
        }) || [0])
      );

      return textMaxWidth + (textMaxWidth ? extraWidth : 0);
    }
    /**  */
    const finalFirstMaxWidth = getFinalTitleMaxWidth('first');
    /**  */
    const finalSecondMaxWidth = getFinalTitleMaxWidth('second');
    /**  */
    const finalThirdMaxWidth = getFinalTitleMaxWidth('third');
    // console.log('evenTitles22', titlesDict, finalFirstMaxWidth);

    // console.log('evenTevenTitles22itles', showMaxWidth, finalFirstMaxWidth, finalSecondMaxWidth, finalThirdMaxWidth);
    // 
    if (showMaxWidth > finalFirstMaxWidth + finalSecondMaxWidth + finalThirdMaxWidth) {
      if (finalThirdMaxWidth) {
        /** ,3 */
        const leftWidth =
          Math.floor((showMaxWidth - (finalFirstMaxWidth + finalSecondMaxWidth + finalThirdMaxWidth)) / 3) || 0;

        return {
          first: finalFirstMaxWidth + leftWidth,
          second: finalSecondMaxWidth + leftWidth,
          third: finalThirdMaxWidth + leftWidth,
        };
      } else if (finalSecondMaxWidth) {
        /** 1 */
        const leftWidth = showMaxWidth - (finalFirstMaxWidth + finalSecondMaxWidth + finalThirdMaxWidth) || 0;

        return {
          first: finalFirstMaxWidth + leftWidth,
          second: finalSecondMaxWidth,
          third: finalThirdMaxWidth,
        };
      }
    }

    return {
      first: finalFirstMaxWidth + 8,
      second: finalSecondMaxWidth + 8,
      third: finalThirdMaxWidth,
    };
  }, [curBlockTitleWidth, defaultTreeData]);

  /** style */
  const getFinalChildStyle = useCallback(
    (tIndex: number) => {
      if (tIndex % 3 === 0) {
        return {
          maxWidth: childBlockWidth.first,
          minWidth: childBlockWidth.first,
          width: childBlockWidth.first,
        };
      } else if (tIndex % 3 === 1) {
        return {
          maxWidth: childBlockWidth.second,
          minWidth: childBlockWidth.second,
          width: childBlockWidth.second,
        };
      } else if (tIndex % 3 === 2) {
        return {
          maxWidth: childBlockWidth.third,
          minWidth: childBlockWidth.third,
          width: childBlockWidth.third,
        };
      }
      return undefined;
    },
    [childBlockWidth]
  );

  // console.log('curBlockTitleWidth', curBlockTitleWidth, childBlockWidth);

  /**  */
  const onCheck = useCallback((e: CheckboxChangeEvent): void => {
    // console.log('sdsdsd', e, e.target.value, e.target.name);

    // 
    if (e.target.checked) {
      setCheckedKeys(origin => {
        return [...origin, e.target.value];
      });

      setSelectedIndicator(origin => {
        return [
          ...origin,
          {
            key: e.target.value,
            title: e.target.name,
          } as ISelectedIndicator,
        ];
      });
    } else {
      setCheckedKeys(origin => {
        return origin.filter(item => {
          return item !== e.target.value;
        });
      });
      // 
      setSelectedIndicator(origin => {
        return origin.filter(selectedItem => selectedItem?.key !== e.target.value);
      });
    }
  }, []);

  /**  */
  const handleCheckAllChange = useCallback(
    (e: CheckboxChangeEvent) => {
      // 
      if (e.target.checked) {
        // const finalSelectedKeys = [
        //   ...checkedKeys,
        //   ...(nativeAllSelectedIndicators?.filter(item => !checkedKeys?.includes(item?.key))?.map(t => t?.key) || []),
        // ];
        setCheckedKeys([...(nativeAllSelectedIndicators?.map(item => item?.key) || [])]);

        setSelectedIndicator([...nativeAllSelectedIndicators]);
        // setSelectedIndicator(
        //   finalSelectedKeys
        //     ?.map(item => {
        //       return nativeAllSelectedIndicators?.find(t => item === t?.key);
        //     })
        //     ?.filter(x => x) as IHandledSelectedIndicator[]
        // );
      } else {
        // 
        setCheckedKeys(nativeAllSelectedIndicators?.filter(item => item?.lock)?.map(t => t?.key) || []);
        setSelectedIndicator(nativeAllSelectedIndicators?.filter(item => item?.lock));
      }
    },
    [nativeAllSelectedIndicators]
  );

  /**  */
  const handleClose = useCallback(() => {
    setSelectedIndicator(nativeSelectedIndicator?.slice() || []);
    setCheckedKeys(nativeCheckedKeys?.slice() || []);
    onVisibleChange(false);
  }, [onVisibleChange, nativeSelectedIndicator, nativeCheckedKeys]);

  /**  */
  const handleOk = useCallback(async () => {
    updateViewDetail?.(selectedIndicator, curIndicatorTreeData);
    setNativeSelectedIndicator(selectedIndicator?.slice() || []);
    setNativeCheckedKeys(checkedKeys?.slice() || []);
    onVisibleChange(false);
  }, [onVisibleChange, selectedIndicator, updateViewDetail, curIndicatorTreeData, checkedKeys]);

  /**  */
  const handleReset = useCallback(() => {
    setCheckedKeys(nativeCheckedKeys?.slice() || []);
    setSelectedIndicator(nativeSelectedIndicator?.slice() || []);
  }, [nativeCheckedKeys, nativeSelectedIndicator]);

  // 
  useEffect(() => {
    // console.log('sdsd', defaultTreeData);
    // 
    setCurIndicatorTreeData(defaultTreeData);
    /** tree */
    const curAllNewSelectedIndicators = getAllTreeNodeKeys(defaultTreeData);
    // 
    setNativeAllSelectedIndicators(curAllNewSelectedIndicators);
    const defaultCheckedKeys = defaultSelectedList?.map(item => item.key) || [];
    setCheckedKeys(defaultCheckedKeys);
    setNativeCheckedKeys(defaultCheckedKeys?.slice());
    setSelectedIndicator(defaultSelectedList || []);
    setNativeSelectedIndicator(defaultSelectedList?.slice() || []);

    setTotal(getTotalIndicator(defaultTreeData));
  }, [defaultTreeData, defaultSelectedList]);

  return (
    <Modal
      title=""
      width={727}
      onCancel={handleClose}
      visible={visible}
      bodyStyle={{
        minHeight: 263,
        maxHeight: 357,
        overflowY: 'auto',
        overscrollBehavior: 'contain',
        padding: '24px 0px',
      }}
      footer={
        <div className={cn('merchantsViewModalFooter')}>
          <div className={cn('merchantsViewModalFooter-show')}>
            {!isSelectedNumHidden && (
              <div className={cn('merchantsViewModalFooter-show-desc')}>
                {selectedIndicator?.length}/{total}
              </div>
            )}
            <div className={cn('merchantsViewModalFooter-show-reset')} onClick={handleReset}>
              
            </div>
          </div>
          <Button className={cn('merchantsViewModalFooter-confirm')} onClick={handleOk} type="primary">
            {' '}
          </Button>
          <Button className={cn('merchantsViewModalFooter-cancel')} onClick={handleClose} type="dashed">
            {' '}
          </Button>
        </div>
      }>
      <div className={cn('merchantsViewModalColumnTree')}>
        {showSelectAll && (
          <div className={cn('merchantsViewModalColumnTree-block')}>
            <div
              className={cn('merchantsViewModalColumnTree-block-title')}
              style={{
                width: curBlockTitleWidth,
                marginRight: curBlockTitleWidth ? 16 : 0,
              }}>
              
            </div>
            <div className={cn('merchantsViewModalColumnTree-block-group')}>
              <div className={cn('merchantsViewModalColumnTree-block-group-item')}>
                <Checkbox
                  disabled={isDisabledSelectAll}
                  checked={isCheckedSelectAll}
                  indeterminate={isIndeterminateSelectAll}
                  onChange={handleCheckAllChange}>
                  
                </Checkbox>
              </div>
            </div>
          </div>
        )}
        {curIndicatorTreeData?.map((item, index) => {
          return (
            <div key={`${item?.title}=${index}`} className={cn('merchantsViewModalColumnTree-block')}>
              <div
                className={cn('merchantsViewModalColumnTree-block-title')}
                style={{
                  width: curBlockTitleWidth,
                  marginRight: curBlockTitleWidth ? 16 : 0,
                }}>
                {item?.title}
              </div>
              <div className={cn('merchantsViewModalColumnTree-block-group')}>
                {item?.children?.map((t, tIndex) => {
                  return (
                    <div
                      key={`${t?.title}-${t?.key}-${tIndex}`}
                      className={cn('merchantsViewModalColumnTree-block-group-item')}
                      style={getFinalChildStyle(tIndex)}>
                      <Checkbox
                        disabled={t?.lock}
                        checked={Boolean(checkedKeys?.includes(t?.key))}
                        value={t?.key}
                        onChange={onCheck}
                        name={t?.title}>
                        {t?.title}
                      </Checkbox>
                    </div>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>
    </Modal>
  );
};

export default SetColumnsModal;
