export const COMMODITY_CATEGORY = 'AllCid';
let _cacheCategoryData = {};
export const HAS_COUPON = 'has_coupon';
export const HAS_INVOICE = 'invoicing_support';
export const HAS_RANK = 'weekly_sales_chart';

export const getFilterLabelLogParams = (item, query) => {
  let params = {
    filter: '',
    sort_field: '',
    listed_info: item?.leaderboard_info?.rank_text,
    is_coupon: item?.is_lucky_coupon ? 1 : 0,
  };
  if (query?.common_filter?.[COMMODITY_CATEGORY]) {
    const sub_fields = query?.common_filter?.[COMMODITY_CATEGORY]?.sub_fields || [];
    if (sub_fields?.length) {
      params.filter += `:${sub_fields.map(it => _cacheCategoryData[it]).join(',')}`;
    }
  }
  if (query?.common_filter?.Price) {
    const range = query?.common_filter?.Price?.range;
    if (range?.min && range?.max) {
      params.filter += `;:${(range?.min || 0) / 100}-${(range?.max || 0) / 100}`;
    }
  }
  if (query?.common_filter?.[HAS_COUPON]) {
    params.filter += `;`;
  }
  if (query?.common_filter?.[HAS_INVOICE]) {
    params.filter += `;`;
  }
  if (query?.common_filter?.[HAS_RANK]) {
    params.filter += `;`;
  }
  if (query?.search_text) {
    params.query = query.search_text;
  }
  if (query?.order_by) {
    if (query.order_by === 'month_sales') {
      params.sort_field = '';
    } else if (query.order_by === 'supply_price') {
      const order = { 1: '', 2: '' }[query.sort];
      params.sort_field = `${order}`;
    }
  } else {
    params.sort_field = '';
  }
  return params;
};
