import numeral from 'numeral';

export interface IScales {
  minLimits: number[];
  scales?: string[];
}

export enum ApproximateType {
  ROUND,
  FLOOR,
  CEIL
}

const ApproximateTypeMap = {
  [ApproximateType.ROUND]: Math.round,
  [ApproximateType.FLOOR]: Math.floor,
  [ApproximateType.CEIL]: Math.ceil,
};

export class NumeralChain {
  curNumeral: numeral.Numeral; // numeral
  formatStr: string; // 
  approximateType: ApproximateType; // 
  matchedScaleIndex: number | null; // 
  scaleLength: number; // 
  prefix: string; // 
  suffix: string; // 

  constructor(input?: unknown) {
    this.curNumeral = numeral(input);
    this.formatStr = '';
    this.approximateType = ApproximateType.ROUND;
    this.matchedScaleIndex = null;
    this.scaleLength = 0;
    this.prefix = '';
    this.suffix = '';
  }

  // 
  getMatchedRule(scaleRule: string | IScales, reg: RegExp) {
    let rule = '';
    let matchedScaleIndex: number | null = null;
    let scaleLength = 0;

    if (typeof scaleRule === 'string') {
      if (!reg.test(scaleRule)) {
        throw new Error(':[*/]+"/100"');
      }

      rule = scaleRule;
      scaleLength = 1;
    } else {
      const { minLimits, scales } = scaleRule;

      if (!minLimits.length) {
        throw new Error('');
      }

      if (scales && minLimits.length !== scales.length) {
        throw new Error('');
      }

      let rules: string[] = [];
      if (!scales) {
        rules = Array(minLimits.length).fill('');
      } else {
        rules = [...scales];
      }

      for (let i = 0; i < rules.length; i++) {
        if (rules[i] && !reg.test(rules[i])) {
          throw new Error(`${i + 1}:[*/]+'/100'`);
        }
      }

      const { length } = rules;
      const value: number = this.curNumeral.value() as number;

      scaleLength = length;

      // minLimits
      // i = 0 --> [-Infinity, minLimits[1])
      // i = 1 --> [minLimits[1], minLimits[2])
      // i = 2 --> [minLimits[2], minLimits[3])
      // ...
      // i = minLimits.length - 1 --> [minLimits[minLimits.length - 1], Infinity)

      // rules[i]ichoiceFormataddSuffixi
      for (let i = 0; i < length; i++) {
        if (i === 0 && value < minLimits[1]) {
          // minLimits[1]rules[0]
          rule = rules[0];
          matchedScaleIndex = 0;
          break;
        }
        if (i >= length - 1) {
          // minLimitsrules
          rule = rules[length - 1];
          matchedScaleIndex = length - 1;
          break;
        }
        if (value >= minLimits[i] && value < minLimits[i + 1]) {
          // minLimits[i]minLimits[i + 1]rules[i]
          rule = rules[i];
          matchedScaleIndex = i;
          break;
        }
      }
    }

    return { rule, matchedScaleIndex, scaleLength };
  }

  // 
  scale(scaleRule: string | IScales) {
    if (typeof this.curNumeral.value() !== 'number') {
      throw new Error('');
    }

    if (!scaleRule) {
      throw new Error('');
    }

    if (this.scaleLength) {
      throw new Error('');
    }

    const reg = /^([*/])(\d+)$/;
    const { rule, matchedScaleIndex, scaleLength } = this.getMatchedRule(scaleRule, reg);

    const passTest = reg.test(rule);

    if (passTest) {
      const [numStr] = rule.split(/[*/]/).filter(Boolean);
      const num = Number(numStr);

      if (rule.includes('*')) {
        this.curNumeral.multiply(num);
      } else if (rule.includes('/')) {
        if (num === 0) {
          throw new Error('0');
        }

        this.curNumeral.divide(num);
      }
    }

    this.matchedScaleIndex = matchedScaleIndex;
    this.scaleLength = scaleLength;
    return this;
  }

  // 
  choiceFormat(format: string | string[], approximateType: ApproximateType = ApproximateType.ROUND) {
    if (Array.isArray(format)) {
      if (!format.length) {
        throw new Error('');
      }

      if (!this.scaleLength) {
        throw new Error('scale');
      }

      if (format.length !== this.scaleLength) {
        throw new Error('scale');
      }

      this.formatStr = format[this.matchedScaleIndex as number];
    } else {
      this.formatStr = format;
    }
    this.approximateType = approximateType;
    return this;
  }

  // 
  addPrefix(prefix: string) {
    this.prefix = prefix + this.prefix;
    return this;
  }

  // 
  addSuffix(suffix: string | string[]) {
    if (Array.isArray(suffix)) {
      if (!suffix.length) {
        throw new Error('');
      }

      if (!this.scaleLength) {
        throw new Error('scale');
      }

      if (suffix.length !== this.scaleLength) {
        throw new Error('scale');
      }

      this.suffix = this.suffix + suffix[this.matchedScaleIndex as number];
    } else {
      this.suffix = this.suffix + suffix;
    }
    return this;
  }

  // ++
  toString() {
    return `${this.prefix}${this.curNumeral.format(this.formatStr, ApproximateTypeMap[this.approximateType])}${
      this.suffix
    }`;
  }

  // []
  toArr() {
    return [this.prefix, this.curNumeral.format(this.formatStr, ApproximateTypeMap[this.approximateType]), this.suffix];
  }
}

export default function numeralChain(input?: unknown) {
  return new NumeralChain(input);
}
